// @ts-nocheck
import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import axios from "axios";
import path from "./../../../const/path";

const config = {
};

const namespace = "seeker";
const pathToSeeker = `${path.api_url}/seeker`;

export const getSeeker = createAsyncThunk(
  `${namespace}/getSeeker`, 
  async () => {
    const response = await axios.get(pathToSeeker, config);
    return response.data;
  }
);

export const seekerSlice: any = createSlice({
  name: namespace,
  initialState: {
    seeker: [],
    loading: true,
    error: null,
  },
  reducers: {
    getSeeker: (state, action) => {
      state.seeker = action.payload;
    },
  },
  extraReducers: {
    [getSeeker.pending]: (state: any, action: any) => {
      state.loading = true;
    },
    [getSeeker.fulfilled]: (state: any, action: any) => {
      state.loading = false;
      state.seeker = action.payload;
    },
    [getSeeker.rejected]: (state: any, action: any) => {
      state.loading = false;
      state.error = action.error.message;
    },
  },
});

export default seekerSlice;
